import { Component, OnInit } from '@angular/core'; import { Router, ActivatedRoute } from '@angular/router'; import { Subscription } from 'rxjs/Subscription';  import { Escalonamento_usuario } from '../../../modelo/Escalonamento_usuario'; import { ObjetoVirtual } from '../../../modelo/ObjetoVirtual'; import { PesquisaService } from '../../../core/services/pesquisa/pesquisa.service';  @Component({   moduleId: module.id,   selector: 'escalonamento_usuario-dados-edit',   templateUrl: 'escalonamento_usuario_dadosEdit.component.html' }) export class Escalonamento_usuarioDadosEditComponent implements OnInit {    escalonamento_usuario: Escalonamento_usuario;    private sub: Subscription;    private errorMessage: string;     constructor(private router: Router,               private route: ActivatedRoute,               private pesquisaService: PesquisaService) { }    ngOnInit() {       this.sub = this.route.parent.params.subscribe(params => {         let o : ObjetoVirtual = new ObjetoVirtual();         o.classeJava = "br.com.primum.modelo.escalonamento_usuario.Escalonamento_usuario";                 let id = +params['id'];         this.escalonamento_usuario = new Escalonamento_usuario();         this.escalonamento_usuario.cd_escalonamento_usuario = id;         o.objetoJson = this.escalonamento_usuario;          this.pesquisaService.efetuarPesquisa(o)             .subscribe(             result => this.onEfetuarPesquisa(result),             error =>  this.errorMessage = <any>error);       });   }    onEfetuarPesquisa(result : any) {     if (!result) {       this.errorMessage = 'Houve um erro ao pesquisa a escalonamento_usuario. ';       }      this.escalonamento_usuario = result;   }    ngOnDestroy() {     this.sub.unsubscribe();   }     onSubmit() {         }     onCancel(event: Event) {     event.preventDefault();     this.router.navigate(['/']);   }  }


